.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;

  color: #fff;

  @include onTablet {
    font-size: 48px;
    margin-bottom: 48px;
  }

  @include onDesktop {
    font-size: 64px;
    margin-bottom: 48px;
  }
}

.section-title-first {
  text-align: left;

  @include onTablet {
    text-align: left;
    margin-bottom: 0;
  }
  @include onDesktop {
    text-align: left;
    margin-bottom: 0;
  }
}

.about-us__description {
  font-weight: 500;
  font-size: 23px;
  line-height: 140%;
}
